<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="sets" highlight-current-row v-loading="listLoading"  style="width: 100%;">
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="COLLECTION_NAME" label="集合名称" width="200" sortable>
			</el-table-column>
			<el-table-column prop="creator" label="创建人" width="200"  sortable>
			</el-table-column>
			<el-table-column prop="time" label="创建时间" width="250"  sortable>
			</el-table-column>
			<el-table-column prop="note" label="备注" width="300" sortable>
			</el-table-column>
			<el-table-column label="操作" width="150">
				<template scope="scope">
					<el-button size="small" @click="changeToSet(scope.$index, scope.row)">切换</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form ref="form" :model="form" label-width="100px" @submit.prevent="onSubmit" v-loading="loading" style="margin:20px;width:60%;min-width:600px;">
				<el-form-item label="统计名单名称">
					<el-input v-model="form.DatabaseName" placeholder="请输入英文，不要出现中文字符"></el-input>
				</el-form-item>
				<el-form-item label="创建人">
					<el-input v-model="form.creator"></el-input>
				</el-form-item>
				<el-form-item label="创建时间">
					<el-col :span="11">
						<el-date-picker type="date" placeholder="选择日期" v-model="form.time" style="width: 100%;"></el-date-picker>
					</el-col>
				</el-form-item>
				<el-form-item label="备注">
					<el-input type="textarea" v-model="form.note"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="onSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import { addSet , getSetAll , changeSet , removeSet } from '../../api/api';
	export default {
		data() {
			return {
				sets:[],
				form: {
					DatabaseName: '',
					creator:'',
					time: '',
					note: ''
				},
				loading: false,


				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
					DatabaseName: [
						{ required: true, message: '请输入集合名称', trigger: 'blur' },
						{ min: 2,  trigger: 'blur' }
					],
					creator:[{ required: true, message: '请输入姓名', trigger: 'blur' }],


				},
				addForm: {
					DatabaseName: '',
					creator:'',
					time:'',
					note:''		
				}
			}
		},
		methods: {
			//得到所有集合并显示
			getAllSets() {
				this.listLoading = true;
				getSetAll({}).then((res) => {
					let allset = res.data.all;
					allset.filter((element) => {
						this.sets.push(JSON.parse(element));
					})
					this.listLoading = false;
				})
			},
			//删除集合
			handleDel: function (index, row) {
				this.$confirm('确认删除该集合吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = Object.assign({}, row);
					removeSet({DeleteDatabaseName: para.COLLECTION_NAME}).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						location.reload();
					});
				}).catch(() => {

				});
			},
			//切换集合
			changeToSet:function (index, row) {
				this.$confirm('确认切换到该集合吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					let para = Object.assign({}, row);
					changeSet(para).then((res) => {
						this.listLoading = false;
						this.$message({
							message: '切换成功',
							type: 'success'
						});
						location.reload();
						//this.getAllSets();
					});
				}).catch(() => {

				});
			},
			//显示新增页面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					DatabaseName: '',
					creator:'',
					time:'',
					note:''
				};
			},
			//提交新增集合
			onSubmit() {
				let params = {
					DatabaseName: this.form.DatabaseName,
					creator: this.form.creator,
					time:  this.form.time,
					note:  this.form.note
				}
				this.loading = true;
				if ((params)){
					addSet(params).then((res) => {
						if (res.status === 200 && res.data === 'OK'){
							this.loading = false;
							this.$message({
									message: '提交成功',
									type: 'success'
								});
							this.addFormVisible = false;
							location.reload();
						} else {
							this.loading = false;
							this.$message({
									message: '提交失败',
									type: 'error'
								});
							this.addFormVisible = false;
							this.getAllSets();
						}
					})
				} else {
					this.loading = false;
					alert('添加失败(参数不正常)');
				}


			}
		},
		mounted() {
			this.getAllSets();
		}
	}

</script>


