import axios from 'axios';

let base = 'http://localhost:3030/messages';
let setBase = 'http://localhost:3030/database'

export const requestLogin = params => { 
    return axios.post(`http://localhost:3030/AdminLogin`, params).then(res => res.data);
};

export const getUserList = params => {
    if (params.name === '' || params.name === "姓名"){
        return axios.get(`${base}`);
    } else {
        return axios.post(`${base}/find`, params);
    }
};


export const getUserListPage = params => { 
    return axios.post(`${base}/page`, params);
};

export const removeUser = params => {
     return  axios({
        method: 'delete',
        url: `${base}`,
        data: params
      });
};

export const batchRemoveUser = params => {
     return  axios({
        method: 'delete',
        url: `${base}/batchremove`,
        data: params
      });
    
};

export const editUser = params => {
     return axios.put(`${base}`, params); 
};

export const addUser = params => { 
    return axios.post(`${base}`, params); 
};

export const addSet = params => { 
    return axios.post(`${setBase}`, params); 
};

export const getSet = params => { 
    return axios.get(`${setBase}`); 
};

export const getSetAll = params => {
    return axios.get(`${setBase}/all`);
}

export const changeSet = params => {
    return axios.post(`${setBase}/change`, params);
}

export const removeSet = params => {
    return axios({
        method: 'delete',
        url: `${setBase}`,
        data: params
      });
}



